@font-face {
  font-family: "GorditaMedium";
  src: url(../../fonts/GorditaMedium.otf);
  font-weight: 100;
  font-display: swap;
}
@font-face {
  font-family: "GorditaRegular";
  src: url(../../fonts/GorditaRegular.otf);
  font-weight: 100;
  font-display: swap;
}
@font-face {
  font-family: "GorditaBold";
  src: url(../../fonts/GorditaBold.otf);
  font-weight: 100;
  font-display: swap;
}
@font-face {
  font-family: "GorditaBlack";
  src: url(../../fonts/GorditaBlack.otf);
  font-weight: 100;
  font-display: swap;
}
/** Media Queries **/
html {
  overflow-x: hidden;
}

body {
  font-size: 16px;
}

picture {
  display: block;
}

.w-100, .wh-100 {
  width: 100%;
}

.h-100, .wh-100 {
  height: 100%;
}

img {
  vertical-align: top;
  height: auto;
}

.p-relative {
  position: relative;
}

.row.start {
  align-items: flex-start !important;
}

.p-absolute {
  position: absolute;
}

a, button, input[type=submit] {
  cursor: pointer;
  transition: all 0.25s ease;
}

main, section, header, footer, aside {
  width: 100%;
}

a {
  text-decoration: none;
}

textarea, button, input, select, option {
  outline: none;
}

.contenedor {
  width: min(88%, 1500px);
  margin-inline: auto;
}
.contenedor.xl {
  width: min(94%, 1760px);
}

.hr {
  width: 100%;
  height: 2px;
  display: block;
  background: #2A344F;
}

video {
  display: block;
  width: 100%;
  aspect-ratio: 16/9;
}

a {
  font-size: 1.1rem;
}

h1 {
  font-size: 3.85em;
  line-height: 1.2em;
}

h2 {
  font-size: 2.85em;
  line-height: 1.2em;
  font-family: "GorditaBold";
}
h2.up {
  font-size: 3em;
}
h2.down {
  font-size: 2.2em;
}

h3 {
  font-size: 1.65em;
  line-height: 1.2em;
}

p {
  font-size: 1.1rem;
}

form:not(.search-form) {
  padding: 2.5rem 3rem;
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 14px;
}
form:not(.search-form) .wpcf7-response-output {
  border: none;
  text-align: center;
  padding: 0;
  margin-bottom: 0;
}
form:not(.search-form) .wpcf7-spinner {
  position: absolute;
  width: 100%;
  inset: 0;
  height: 100%;
  border-radius: 0;
  margin: 0;
}
form:not(.search-form) .wpcf7-spinner::before {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
form:not(.search-form) input, form:not(.search-form) textarea, form:not(.search-form) button {
  outline: none;
}
form:not(.search-form) input {
  border-radius: 5px;
}
form:not(.search-form) input:not([type=submit]), form:not(.search-form) textarea {
  border: 1px solid #707070;
  background: white;
  color: #707070;
  margin: 0;
  width: 100%;
  min-height: 48px;
  padding-inline: 12px;
}
form:not(.search-form) textarea {
  resize: none;
  padding-block: 12px;
  max-height: 120px;
}
form:not(.search-form) [type=submit] {
  background: #000;
  border: 2px solid #000;
  text-align: center;
  width: fit-content;
  margin: 0 auto;
  color: white;
  min-height: 52px;
  padding-inline: 26px;
}
form:not(.search-form) [type=submit]:hover {
  background: transparent;
  color: #009A17;
  border-color: #009A17;
}
form:not(.search-form) > p {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 10px;
  flex-direction: column;
}
form:not(.search-form) > p span {
  width: 100%;
}